  
.Container {
    position: relative;
    min-height: 300px;
    max-height: 800px;
    
    
}

/* Style the video: 100% width and height to cover the entire window */
.Video {
    width: 100%;
    height: 100%;
  }
  
  /* Add some content at the bottom of the video/page */
  .Content {
    position: absolute;
    top: 0;
    color:#f1f1f1;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
    
  }
  .SubContent {
    border: 3px solid rgba(95, 20, 20, 0.90);
      text-align: center;
      padding: 10px;
      background-color: rgba(251, 232, 210, 0.90);
      border-radius:5rem ;
      width: 32rem;
      height: 26rem;
      margin-top: 60px;
    }
  .SubContent p {
      color: rgb(95, 20, 20);
      font-size: 40px;
      padding: 16% 0 5%;
      font-weight: bold;
  }
  .SubContent button {
    font-weight: bold;
    margin-top: 40px;
    border: rgb(95, 20, 20) 2px solid;
    color: rgb(212, 189, 161);
    background-color: rgba(95, 20, 20, 1);
    padding: 20px 5px;
    font-size: 1.25rem;
    border-radius: 5px;
    cursor:pointer;
    width: 12rem;
    font-size: 2rem;
}
.SubContent button:hover {
    background-color: rgb(212, 189, 161);
    color: rgb(95, 20, 20);
}

